import { google } from "googleapis";
import { NextResponse } from "next/server";
import { parse } from "json2csv";
import ExcelJS from "exceljs";

const SHEET_ID = process.env.SHEET_ID || "";

export async function POST(req: Request) {
  try {
    const { startDate, endDate, format } = await req.json(); // üîπ `fileType` ‚Üí `format` „Å´Áµ±‰∏Ä

    console.log("üì§ API Âèó‰ø°:", { startDate, endDate, format });

    const auth = new google.auth.GoogleAuth({
      credentials: {
        type: "service_account",
        project_id: process.env.GOOGLE_PROJECT_ID,
        private_key: process.env.GOOGLE_PRIVATE_KEY?.replace(/\\n/g, "\n"), // üîπ ÊîπË°å„ÇíÂé≥ÂØÜ„Å´Âá¶ÁêÜ
        client_email: process.env.GOOGLE_CLIENT_EMAIL,
      },
      scopes: ["https://www.googleapis.com/auth/spreadsheets.readonly"],
    });

    const sheets = google.sheets({ version: "v4", auth });

    // üîπ „Çπ„Éó„É¨„ÉÉ„Éâ„Ç∑„Éº„Éà„Åã„Çâ„Éá„Éº„Çø„ÇíÂèñÂæó
    const response = await sheets.spreadsheets.values.get({
      spreadsheetId: SHEET_ID,
      range: "TimeSheet!A:J", // üîπ ÂèñÂæóÁØÑÂõ≤„ÇíÊòéÁ¢∫„Å´ÊåáÂÆö
    });

    const rows = response.data.values || [];
    console.log("üìä ÂèñÂæó„Éá„Éº„Çø:", rows.length, "‰ª∂");

    if (rows.length < 2) throw new Error("„Éá„Éº„Çø„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì");

    // üîπ „Éò„ÉÉ„ÉÄ„Éº„Å®„Éá„Éº„Çø„ÇíÂàÜ„Åë„Çã
    const headers = rows[0];
    
    const data = rows.slice(1).filter(row => {
        const rowStart = new Date(row[4]); // `start` (EÂàó) „Çí `Date` „Å´Â§âÊèõ
        const rowEnd = new Date(row[5]);   // `end` (FÂàó) „Çí `Date` „Å´Â§âÊèõ
      
        // ‚úÖ `YYYY-MM-DD` „ÅÆÊñáÂ≠óÂàó„Å®„Åó„Å¶ÊØîËºÉ
        const rowStartDate = rowStart.toISOString().split("T")[0]; // `YYYY-MM-DD` ÂΩ¢Âºè„Å´Â§âÊèõ
        const rowEndDate = rowEnd.toISOString().split("T")[0];     // `YYYY-MM-DD` ÂΩ¢Âºè„Å´Â§âÊèõ
      
        console.log("üìù „Éá„Éº„ÇøÁ¢∫Ë™ç:", rowStartDate, rowEndDate);
      
        return rowStartDate >= startDate && rowEndDate <= endDate;
    });
      
      

    console.log("üìä „Éï„Ç£„É´„ÇøÂæå:", data.length, "‰ª∂");

    if (!data.length) throw new Error("ÊåáÂÆöÊúüÈñì„ÅÆ„Éá„Éº„Çø„Åå„ÅÇ„Çä„Åæ„Åõ„Çì");

    let fileBuffer;
    let contentType;
    let fileExtension;

    if (format === "csv") {
      // üîπ CSV ÂΩ¢Âºè„Åß„Ç®„ÇØ„Çπ„Éù„Éº„Éà
      const csv = parse(data, {
        fields: headers,
        quote: '"', // üîπ ÂÄ§„Çí„Ç®„Çπ„Ç±„Éº„ÉóÂá¶ÁêÜ
        delimiter: ",",
      });

      fileBuffer = Buffer.from(csv, "utf-8");
      contentType = "text/csv";
      fileExtension = "csv";
    } else {
      // üîπ Excel ÂΩ¢Âºè„Åß„Ç®„ÇØ„Çπ„Éù„Éº„Éà
      const workbook = new ExcelJS.Workbook();
      const worksheet = workbook.addWorksheet("Exported Data");

      worksheet.addRow(headers); // üîπ „Éò„ÉÉ„ÉÄ„ÉºË°å„ÇíËøΩÂä†
      data.forEach(row => worksheet.addRow(row));

      fileBuffer = await workbook.xlsx.writeBuffer();
      contentType = "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
      fileExtension = "xlsx";
    }

    console.log("üìÇ „Éï„Ç°„Ç§„É´ÁîüÊàêÂÆå‰∫Ü: export." + fileExtension);

    // üîπ „ÇØ„É©„Ç§„Ç¢„É≥„Éà„Å´„Éï„Ç°„Ç§„É´„ÇíÈÄÅ‰ø°
    return new Response(fileBuffer, {
      headers: {
        "Content-Type": contentType,
        "Content-Disposition": `attachment; filename="export_${startDate}_${endDate}.${fileExtension}"`,
      },
    });
  } catch (error) {
    console.error("‚ùå „Ç®„ÇØ„Çπ„Éù„Éº„Éà„Ç®„É©„Éº:", error);
    return NextResponse.json({ 
      error: (error as Error).message, 
      stack: (error as Error).stack // ‚úÖ ËøΩÂä†: „Çπ„Çø„ÉÉ„ÇØ„Éà„É¨„Éº„Çπ„ÇíË°®Á§∫
    }, { status: 500 });
  }
  
}
